.RetroIntro {
  --color-hot-pink: 255, 18, 255;

  background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, #111111), color-stop(60%, #040C4A));

  background: -webkit-linear-gradient(top, #111111 10%, #040C4A 60%);

  background: -o-linear-gradient(top, #111111 10%, #040C4A 60%);

  background: linear-gradient(to bottom, #111111 10%, #040C4A 60%);
  width: 100vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.RetroIntro__background {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.RetroIntro__background canvas {
  position: absolute;
}

.RetroIntro__text {
  text-align: center;
  position: relative;
}

.RetroIntro__title {
  margin: 0;
}

.RetroIntro__title-word {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 100px;
}

.RetroIntro__title-sub {
  font-weight: normal;
  text-transform: uppercase;
  font-family: 'Lazer84', sans-serif;
  color: rgba(var(--color-hot-pink), 1);
  font-size: 90px;
  letter-spacing: 0.05em;
  -webkit-transform: rotate(-4deg);
      -ms-transform: rotate(-4deg);
          transform: rotate(-4deg);
  position: relative;
  top: -40px;
  left: -10px;
  text-shadow: rgba(var(--color-hot-pink), 0.5) 0 0 60px;
}

.RetroIntro__title-sub:before {
  content: '+';
  margin-right: 10px;
}

.RetroIntro__name {
  text-transform: uppercase;
  font-size: 24px;
  letter-spacing: 0.2em;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 0;
  margin-top: 10px;
  font-weight: 500;
  text-align: center;
}



